import React from "react";

const Product = () => {
  return (
    <>
      <div className="col-md-4 col-12 d-flex flex-row justify-content-center align-items-center gap-2">
        <img
          src="https://www.utsavpedia.com/wp-content/uploads/2014/01/Chunari13.jpg"
          alt="bandhani"
          className="product_img"
        />
        <div className="card-body">
          <div className="card-title fw-bolder fs-4">Bandhani Saree</div>
          <p className="mb-0">Material: Gaji Silk</p>
          <p>Price: Rs.700</p>
        </div>
      </div>
      <hr className="mx-auto my-3 product_divider" />
    </>
  );
};

export default Product;
